const pages = [
    "Description",
    "Downloads",
]

interface NavigationProps {
    page: string
    setPage: (page: string) => void
}
export const Navigation = (props: NavigationProps) => {
    return (
        <Paper sx={{ position: "absolute", top: "0em", bottom: "0em", left: "0em", right: "0em", borderRadius: "0" }}>
            <List disablePadding>
                {pages.map((p) => (
                    <ListItem disablePadding disableGutters classes={{}}>
                        <ListItemButton onClick={() => {props.setPage(p)}} selected={props.page === p}>
                            <Box height="3em" width="100%" display="flex" alignItems="center" justifyContent="left" paddingLeft="0.5em">
                                <Typography fontSize="1.2em">
                                    <b>{p}</b>
                                </Typography>
                            </Box>
                        </ListItemButton>
                    </ListItem>
                ))}
            </List>
        </Paper>
    )
}
export default Navigation