import React from 'react';

import AppBar from '@mui/material/AppBar'
import Box from '@mui/material/Box'
import Button from '@mui/material/Button'
import ButtonBase from '@mui/material/ButtonBase'
import Divider from '@mui/material/Divider'
import Drawer from '@mui/material/Drawer'
import Icon from '@mui/material/Icon'
import IconButton from '@mui/material/IconButton'
import List from '@mui/material/List'
import ListItem from '@mui/material/ListItem'
import ListItemButton from '@mui/material/ListItemButton'
import ListItemIcon from '@mui/material/ListItemIcon'
import ListItemText from '@mui/material/ListItemText'
import Paper from '@mui/material/Paper'
import Popover from '@mui/material/Popover'
import ThemeProvider from '@mui/material/styles/ThemeProvider';
import Toolbar from '@mui/material/Toolbar'
import Tooltip from '@mui/material/Tooltip'
import Typography from '@mui/material/Typography'

import Email from '@mui/icons-material/Email';
import ExpandMore from '@mui/icons-material/ExpandMore';
import ContentCopy from '@mui/icons-material/ContentCopy';

import { useTheme } from '@mui/material/styles';

import github from './icons/github.svg';
import linkedin from './icons/linkedin.svg';

interface HeaderProps {

}
export const Header = (props: HeaderProps) => {

    const theme = useTheme();

    const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(null);
    const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };
    const open = Boolean(anchorEl)

    return (
        <AppBar color="inherit">
            <Toolbar color="inherit" sx={{userSelect: "none"}}>
                <Typography fontSize="1.5em" sx={{ flexGrow: 1, cursor: "default" }}>
                    <a href="https://michaelckearney.github.io" style={{textDecoration: "none", color: "inherit"}}>
                    <Box component="span" display="inline" sx={{cursor: "pointer"}}>
                        <b>Michael Kearney</b>
                    </Box>
                    </a>
                    <b> - Babble</b>
                </Typography>
                <Tooltip title="GitHub">
                    <IconButton edge="end" onClick={() => {window.open("https://github.com/michaelckearney/babble")}}  onDragStart={(e) => e.preventDefault()}>
                        <Icon onDragStart={(e) => e.preventDefault()} sx={{}}>
                            <img height={22} width={22} src={github} alt="" />
                        </Icon>
                    </IconButton>
                </Tooltip>
                <Box width="1em" />
                <Tooltip title="LinkedIn">
                    <IconButton edge="end" onClick={() => {window.open("https://www.linkedin.com/in/mkearney2023")}}  onDragStart={(e) => e.preventDefault()}>
                        <Icon color="inherit" sx={{filter: "invert(1)"}} onDragStart={(e) => e.preventDefault()}>
                            <img height={22} width={22} src={linkedin} alt="" />
                        </Icon>
                    </IconButton>
                </Tooltip>
                
                <Box width="1em" />
                <Tooltip title="Email">
                    <IconButton edge="end" onClick={handleClick}>
                        <Icon>
                            <Email />
                        </Icon>
                    </IconButton>
                </Tooltip>
                <Popover 
                    open={open}
                    anchorEl={anchorEl}
                    onClose={handleClose}
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'right',
                    }}

                >
                    <Box height="3em" display="flex" alignItems="center" justifyContent="left" paddingLeft="1em" paddingRight="1em">
                        <Typography fontSize="1em">
                            michael.c.kearney@outlook.com
                        </Typography>
                        <Box width="1em"/>
                        <Tooltip title="Copy">
                            <Box sx={{cursor: "pointer"}} onClick={() => {
                                handleClose()
                                navigator.clipboard.writeText("michael.c.kearney@outlook.com")
                            }}>
                                <ContentCopy sx={{fontSize: "1em"}} />
                            </Box>
                        </Tooltip>
                    </Box>
                </Popover>
            </Toolbar>
        </AppBar>
    )
}
export default Header