interface DownloadsProps {
    downloads: download[]
}

export const Downloads = (props: DownloadsProps) => {
    return (
        <Box position="absolute" top="1em" bottom="1em" left="1em" right="1em">
            <Grid container columns={12}>
                {props.downloads.map((d, index) => (
                    <Grid item xs={6} sm={6} md={4} lg={3} xl={2} minWidth="20em">
                        <Box height="7em" width="100%" display="flex" alignItems="center" justifyContent="center">
                            <Box height="5em" width="calc(100% - 2em)" onClick={() => {window.open(d.url)}}>
                                <Download download={d} />
                            </Box>
                        </Box>
                    </Grid>
                ))}
            </Grid>
        </Box>
    )
}
export default Downloads