
import Icon from '@mui/material/Icon'

import apple from './icons/apple.svg'
import windows from './icons/windows.svg'
import linux from './icons/linux.svg'
import { SvgIcon } from '@mui/material'

import Download from '@mui/icons-material/Download'

interface DownloadIconProps {
    type: string
}
export const DownloadIcon = (props: DownloadIconProps) => {
    return (
        <Icon color="inherit" onDragStart={(e) => e.preventDefault()}>
            <img height={24} width={25} style={{filter: "invert(1)"}} src={
                props.type === "darwin/arm64" ? apple :
                props.type === "darwin/amd64" ? apple :
                props.type === "windows/arm64" ? windows :
                props.type === "windows/amd64" ? windows :
                props.type === "linux/arm64" ? linux :
                props.type === "linux/amd64" ? linux :
                ""
            } alt="" />
        </Icon>
    )   
}
export default DownloadIcon